.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  display:flex;
  background-color: #e5ebeb;
}

.btn{
width:50px;
height:50px;
background-color: #a3a7af;
color:rgb(10, 10, 10);
font-size: large;
margin:1px;
}
.img{
  width:200px;
  height:100px;
  margin:2px 10px 2px 20px;
  box-shadow: 0px 0px 17px 7px#545555;
  
}
.task2 ul{
  display: flex;
  align-items: stretch;
  width:180px;
  margin:5px 5px 5px 10px;
  font-size: small;
}
.task2 ul li{
  background-color: rgb(18, 18, 19);
  text-align:left;
  color: white;
  margin:5px;
  box-shadow: 0px 0px 17px 7px#545555;
  border-radius: 5px;
  padding:7px 7px 7px 7px;
}
.task2 ul li ul{
  display:block;
  
}
h1{
  color:gold;
}
h3{
  font-weight: bold;
}
.task1{
  background-color: rgb(18, 18, 19);
  text-align:left;
  color: white;
  box-shadow: 0px 0px 17px 7px#545555;
  padding:5px 15px 5px 15px;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
